import './index.css';
import React, { useState, useEffect, useCallback } from 'react';
import $ from 'jquery';
import Wrapper from './Components/Wrapper';
import Bars from './Components/Bars/Bars';
import Store from '../src/Utils/Store';
import StoreApi from '../src/Utils/StoreApi';
import Colors from './Utils/Colors';
import LinearGradients from './Utils/LinearGradients';
import ImageApi from './Utils/ImageApi';

export default function App() {
	const [data, setData] = useState(Store);
	const [bgColor, setBgColor] = useState();
	const [btnColors, setBtnColors] = useState();
	const [btnImages, setBtnImages] = useState();
	const [btnNextColor, setBtnNextColor] = useState();
	const [btnColor, setBtnColor] = useState();
	const [oneSecondBtnColor, setOneSecondBtnColor] = useState();
	const [thirtySecondsBtnColor, setThirtySecondsBtnColor] = useState();
	const [oneMinuteBtnColor, setOneMinuteBtnColor] = useState();
	const [fiveMinutesBtnColor, setFiveMinutesBtnColor] = useState();
	const [tenMinutesBtnColor, setTenMinutesBtnColor] = useState();
	const [bgImage, setBgImage] = useState();
	const [images, setImages] = useState([]);
	const [intervalId, setIntervalId] = useState(null);

	const updateBar = (barTitle, barId) => {
		const bar = data.Bars[barId];
		bar.barTitle = barTitle;

		const newState = {
			...data,
			Bars: {
				...data.Bars,
				[barId]: bar,
			},
		};
		setData(newState);
	};

	const getImageList = async () => {
		setImages(await ImageApi());
	};

	useEffect(() => {
		getImageList();
	}, []);

	const RandomBgColors = useCallback(() => {
		const arrColors = Math.floor(Math.random() * Colors.length);
		const colorsValue = Colors[arrColors];
		setBtnColors(colorsValue);
	}, []);

	const RandomBgImages = useCallback(() => {
		const arrImagesColor = Math.floor(Math.random() * Colors.length);
		const imagesColorValue = Colors[arrImagesColor];
		setBtnImages(imagesColorValue);
	}, []);

	const RandomBgNextColor = useCallback(() => {
		const arrNextColor = Math.floor(Math.random() * Colors.length);
		const NextColorValue = Colors[arrNextColor];
		setBtnNextColor(NextColorValue);
	}, []);

	const RandomBgColor = useCallback(() => {
		const arrColor = Math.floor(Math.random() * Colors.length);
		const colorValue = Colors[arrColor];
		setBtnColor(colorValue);
	}, []);

	const RandomOneSecondBgColor = useCallback(() => {
		const arrOneSecondColor = Math.floor(Math.random() * Colors.length);
		const oneSecondBtnColor = Colors[arrOneSecondColor];
		setOneSecondBtnColor(oneSecondBtnColor);
	}, []);

	const RandomThirtySecondsBgColor = useCallback(() => {
		const arrThirtySecondsColor = Math.floor(Math.random() * Colors.length);
		const thirtySecondsBtnColor = Colors[arrThirtySecondsColor];
		setThirtySecondsBtnColor(thirtySecondsBtnColor);
	}, []);

	const RandomOneMinuteBgColor = useCallback(() => {
		const arrOneMinuteColor = Math.floor(Math.random() * Colors.length);
		const oneMinuteBtnColor = Colors[arrOneMinuteColor];
		setOneMinuteBtnColor(oneMinuteBtnColor);
	}, []);

	const RandomFiveMinutesBgColor = useCallback(() => {
		const arrFiveMinutesColor = Math.floor(Math.random() * Colors.length);
		const fiveMinutesBtnColor = Colors[arrFiveMinutesColor];
		setFiveMinutesBtnColor(fiveMinutesBtnColor);
	}, []);

	const RandomTenMinutesBgColor = useCallback(() => {
		const arrTenMinutesColor = Math.floor(Math.random() * Colors.length);
		const tenMinutesBtnColor = Colors[arrTenMinutesColor];
		setTenMinutesBtnColor(tenMinutesBtnColor);
	}, []);

	const RandomBg = useCallback(() => {
		const arrColor = Math.floor(Math.random() * Colors.length);
		const colorValue = Colors[arrColor];

		const arrLinearGradient = Math.floor(Math.random() * LinearGradients.length);
		const linearGradientValue = LinearGradients[arrLinearGradient];

		const allImages = images.map((image) => {
			return `url(${image.full})`;
		});

		const arrAllImages = Math.floor(Math.random() * allImages.length);
		const imagesValue = allImages[arrAllImages];

		const storeValues = [colorValue, linearGradientValue, imagesValue];

		const arrValue = Math.floor(Math.random() * storeValues.length);

		if (storeValues[arrValue] === colorValue) {
			setBgColor(storeValues[arrValue]);
			setBgImage('');
			localStorage.setItem('storedValue', (document.querySelector('.Index').style.backgroundColor = colorValue));
		} else {
			setBgImage(storeValues[arrValue]);
			setBgColor('');
			localStorage.setItem('storedValue', (document.querySelector('.Index').style.backgroundImage = storeValues[arrValue]));
		}
	}, [images]);

	useEffect(() => {
		if (localStorage.hasOwnProperty('storedSvg')) {
			const html = `<svg
			id='blur'
			role='presentation'
			focusable='false'
			viewBox='0 0 24 24'
			xmlns='http://www.w3.org/2000/svg'
			class='svg-wrap'>
			<path
			d='M6.73534 12.3223C6.36105 11.9162 5.72841 11.8904 5.3223 12.2647C4.91619 12.639 4.89039 13.2716 5.26467 13.6777L8.87678 17.597C9.41431 18.1231 10.2145 18.1231 10.7111 17.6264C10.7724 17.5662 10.7724 17.5662 11.0754 17.2683C11.3699 16.9785 11.6981 16.6556 12.0516 16.3075C13.0614 15.313 14.0713 14.3169 15.014 13.3848L15.0543 13.3449C16.7291 11.6887 18.0004 10.4236 18.712 9.70223C19.0998 9.30904 19.0954 8.67589 18.7022 8.28805C18.309 7.90022 17.6759 7.90457 17.2881 8.29777C16.5843 9.01131 15.3169 10.2724 13.648 11.9228L13.6077 11.9626C12.6662 12.8937 11.6572 13.8889 10.6483 14.8825C10.3578 15.1685 10.0845 15.4375 9.83288 15.6851L6.73534 12.3223Z'
			fill='currentColor'
			></path>
			</svg><svg
			id='blur'
			role='presentation'
			focusable='false'
			viewBox='0 0 24 24'
			xmlns='http://www.w3.org/2000/svg'
			class='svg'>
			<path
			d='M6.73534 12.3223C6.36105 11.9162 5.72841 11.8904 5.3223 12.2647C4.91619 12.639 4.89039 13.2716 5.26467 13.6777L8.87678 17.597C9.41431 18.1231 10.2145 18.1231 10.7111 17.6264C10.7724 17.5662 10.7724 17.5662 11.0754 17.2683C11.3699 16.9785 11.6981 16.6556 12.0516 16.3075C13.0614 15.313 14.0713 14.3169 15.014 13.3848L15.0543 13.3449C16.7291 11.6887 18.0004 10.4236 18.712 9.70223C19.0998 9.30904 19.0954 8.67589 18.7022 8.28805C18.309 7.90022 17.6759 7.90457 17.2881 8.29777C16.5843 9.01131 15.3169 10.2724 13.648 11.9228L13.6077 11.9626C12.6662 12.8937 11.6572 13.8889 10.6483 14.8825C10.3578 15.1685 10.0845 15.4375 9.83288 15.6851L6.73534 12.3223Z'
			fill='currentColor'
			></path>
			</svg>`;

			$('.sideBar-colors-picker')[localStorage.getItem('storedSvg')].insertAdjacentHTML('afterbegin', html);
		}
		if (localStorage.hasOwnProperty('storedBgColors')) {
			setBgColor(localStorage.getItem('storedValue'));
			setBgImage(localStorage.getItem('storedValue'));
			RandomBgColors();
		} else if (localStorage.hasOwnProperty('storedBgPhotos')) {
			RandomBgImages();
			setBgImage(localStorage.getItem('storedValue'));
		} else if (localStorage.hasOwnProperty('storedBgOneSecond')) {
			RandomBg();
			RandomOneSecondBgColor();
			const interval = setInterval(() => {
				RandomBg();
				RandomOneSecondBgColor();
			}, localStorage.getItem('storedInterval'));
			setIntervalId(interval);
			return () => clearInterval(interval);
		} else if (localStorage.hasOwnProperty('storedBgThirtySeconds')) {
			RandomBg();
			RandomThirtySecondsBgColor();
			const interval = setInterval(() => {
				RandomBg();
				RandomThirtySecondsBgColor();
			}, localStorage.getItem('storedInterval'));
			setIntervalId(interval);
			return () => clearInterval(interval);
		} else if (localStorage.hasOwnProperty('storedBgOneMinute')) {
			RandomBg();
			RandomOneMinuteBgColor();
			const interval = setInterval(() => {
				RandomBg();
				RandomOneMinuteBgColor();
			}, localStorage.getItem('storedInterval'));
			setIntervalId(interval);
			return () => clearInterval(interval);
		} else if (localStorage.hasOwnProperty('storedBgFiveMinutes')) {
			RandomBg();
			RandomFiveMinutesBgColor();
			const interval = setInterval(() => {
				RandomBg();
				RandomFiveMinutesBgColor();
			}, localStorage.getItem('storedInterval'));
			setIntervalId(interval);
			return () => clearInterval(interval);
		} else if (localStorage.hasOwnProperty('storedBgTenMinutes')) {
			RandomBg();
			RandomTenMinutesBgColor();
			const interval = setInterval(() => {
				RandomBg();
				RandomTenMinutesBgColor();
			}, localStorage.getItem('storedInterval'));
			setIntervalId(interval);
			return () => clearInterval(interval);
		} else if (localStorage.hasOwnProperty('storedBgNextBackground')) {
			setBgColor(localStorage.getItem('storedValue'));
			setBgImage(localStorage.getItem('storedValue'));
			setBtnNextColor(localStorage.getItem('storedBgNextBackground'));
			RandomBgNextColor();
		} else if (localStorage.hasOwnProperty('storedBgPageLoad')) {
			RandomBg();
			RandomBgColor();
		} else {
			RandomBg();
			RandomBgColor();
		}
	}, [
		RandomBg,
		RandomBgColors,
		RandomBgImages,
		RandomBgNextColor,
		RandomBgColor,
		RandomOneSecondBgColor,
		RandomThirtySecondsBgColor,
		RandomOneMinuteBgColor,
		RandomFiveMinutesBgColor,
		RandomTenMinutesBgColor,
	]);

	const stopInterval = () => {
		clearInterval(intervalId);
	};
	return (
		<StoreApi.Provider
			value={{
				updateBar,
				bgColor,
				bgImage,
				setBgColor,
				btnColors,
				btnImages,
				btnNextColor,
				btnColor,
				oneSecondBtnColor,
				thirtySecondsBtnColor,
				oneMinuteBtnColor,
				fiveMinutesBtnColor,
				tenMinutesBtnColor,
				setBgImage,
				RandomBg,
				RandomBgColors,
				RandomBgImages,
				RandomBgNextColor,
				RandomBgColor,
				RandomOneSecondBgColor,
				RandomThirtySecondsBgColor,
				RandomOneMinuteBgColor,
				RandomFiveMinutesBgColor,
				RandomTenMinutesBgColor,
				images,
				stopInterval,
			}}>
			<div
				className='Index'
				style={{ backgroundColor: bgColor, backgroundImage: bgImage, backgroundSize: 'cover' }}>
				{data.barIds.map((barId) => {
					const bar = data.Bars[barId];
					return (
						<Bars
							key={bar.id}
							barId={bar.id}
							barTitle={bar.barTitle}
						/>
					);
				})}
				<div className='App'>
					<Wrapper />
				</div>
			</div>
		</StoreApi.Provider>
	);
}
