import React, { useState, useLayoutEffect, useRef, useContext } from 'react';
import $ from 'jquery';
import MoreHoriz from '@mui/icons-material/MoreHoriz';
import StoreApi from '../../Utils/StoreApi';

export default function Title({ title, listId }) {
	const [open, setOpen] = useState(false);
	const [newTitle, setNewTitle] = useState(title);
	const { updateListTitle } = useContext(StoreApi);

	/* Keep Textarea Height Auto While Typing */

	const [height, setHeight] = useState(0);
	const div = useRef(null);

	useLayoutEffect(() => {
		setHeight(div.current.scrollHeight);
	}, [newTitle]);

	/* On Clicking Buttons Events */

	const btn1 = (e) => {
		setOpen(!open);

		/*Prevent React Beautiful DND Default Scroll*/
		e.target.scrollIntoView();

		const slider = document.querySelector('.Wrapper');
		/*Scroll Title Into Left View*/

		const titleLeft = e.target /*e.currentTarget*/
			.getBoundingClientRect().left;

		if (slider.scrollLeft <= 20) {
			/*slider.scrollBy*/ slider.scrollTo({
				left: -slider.scrollLeft /*0 With slider.scrollTo (Only)*/,
				behavior: 'smooth',
			});
		} else if (titleLeft < 16) {
			slider.scrollTo({
				left: slider.scrollLeft + titleLeft - 16,
				behavior: 'smooth',
			});
		}

		/*Scroll Title Into Right View*/

		if (slider.clientWidth - titleLeft < 272) {
			slider.scrollTo({
				left: slider.scrollLeft + titleLeft - slider.clientWidth + 272,
				behavior: 'smooth',
			});
		}
	};

	const blur = () => {
		setOpen(!open);
		setNewTitle(newTitle.trim());
		newTitle.trim().length === 0 ? setNewTitle(title) : updateListTitle(newTitle.trim(), listId);
	};

	/* On Key Down Events */

	const keyDown = (e) => {
		if (e.key === 'Enter') {
			document.querySelector('.titles-input').blur();
		} else if (e.key === 'Escape') {
			$(document).trigger('click');
			document.querySelector('.titles-input').blur();
		}
	};

	return (
		<>
			<div
				className='titles-hide'
				ref={div}>
				{newTitle}
			</div>
			{open ? (
				<div
					className='titles-inputWrapper'
					id='blur'>
					<textarea
						style={{ height }}
						maxLength={512}
						value={newTitle}
						onChange={(e) => setNewTitle(e.target.value)}
						onFocus={(e) => e.target.select()}
						className='titles-input'
						autoFocus
						onKeyDown={keyDown}
						onBlur={blur}
						name='text'
						type='text'
						autoComplete='off'
					/>
				</div>
			) : (
				<div
					id='blur'
					onClick={btn1}
					className='titles-wrapper'>
					{title}
				</div>
			)}
			<div className='horiz-wrapper'>
				<MoreHoriz
					fontSize='small'
					className='horiz'
				/>
			</div>
		</>
	);
}
