import React, { useState, useEffect } from 'react';

export default function ToggleBtn() {
	const systemSetting = window.matchMedia('(prefers-color-scheme: dark)');
	const [isDark, setIsDark] = useState(systemSetting);

	const calculateSettingAsThemeString = ({ localStorageTheme, systemSetting }) => {
		if (localStorageTheme !== null) {
			return localStorageTheme;
		}

		if (systemSetting.matches) {
			return 'dark';
		}

		return 'light';
	};

	/*	const defaultSetting = ({ systemSettingDark }) => {
		if (systemSettingDark.matches) {
			return 'dark';
		}

		return 'light';
	};*/

	const updateButton = ({ buttonEl, isDark }) => {
		const newCta = isDark ? 'Dark Mode' : 'Light Mode';
		buttonEl.setAttribute('aria-label', newCta);
		buttonEl.innerText = newCta;
	};

	const updateThemeOnHtmlEl = ({ theme }) => {
		document.querySelector('html').setAttribute('data-theme', theme);
	};

	useEffect(() => {
		const removeBtn = document.querySelector('[data-theme-toggle]');
		removeBtn.removeEventListener('click', () => {});
		const button = document.querySelector('[data-theme-default]');
		const updateThemeOnHtml = () => {
			document.querySelector('html').setAttribute('data-theme', isDark ? 'dark' : 'light');
		};
		const localStorageTheme = localStorage.getItem('theme');
		let currentSetting = calculateSettingAsThemeString({ localStorageTheme, systemSetting });

		updateThemeOnHtml({ theme: currentSetting });

		/*		button.addEventListener('click', () => {
			setIsDark((toggle) => !toggle);
			localStorage.setItem('theme', currentSetting);
		});*/
	}, [isDark, systemSetting]);

	useEffect(() => {
		const button = document.querySelector('[data-theme-toggle]');
		const localStorageTheme = localStorage.getItem('theme');
		let currentThemeSetting = calculateSettingAsThemeString({ localStorageTheme, systemSetting });

		//let currentDefaultSetting = calculateSettingAsThemeString({ systemSetting });

		/*		let currentTheme = calculateSettingAsThemeString({ systemSettingDark });
		 */
		updateButton({ buttonEl: button, isDark: currentThemeSetting === 'dark' });
		updateThemeOnHtmlEl({ theme: currentThemeSetting });

		button.addEventListener('click', () => {
			const newTheme = currentThemeSetting === 'dark' ? 'light' : 'dark';

			localStorage.setItem('theme', newTheme);
			updateButton({ buttonEl: button, isDark: newTheme === 'dark' });
			updateThemeOnHtmlEl({ theme: newTheme });

			currentThemeSetting = newTheme;
		});
		/*const systemDefault = ({ systemSettingDark }) => {
			if (systemSettingDark.matches === 'dark') {
				return 'dark';
			} else if (systemSettingDark.matches === 'light') {
				return 'light';
			}
		};*/
		/*const getPreferredScheme = () => (window?.matchMedia?.('(prefers-color-scheme:dark)')?.matches ? 'dark' : 'light');*/
		/*function getTheme() {
			if (window.matchMedia && window.matchMedia('(prefers-color-scheme:dark)').matches) {
				return 'dark';
			} else {
				return 'light';
			}
		}*/
		/*const theme = window.matchMedia?.('(prefers-color-scheme: dark)').matches ? 'dark' : 'light';*/
		button.addEventListener('contextmenu', (e) => {
			e.preventDefault();
			window.matchMedia('(prefers-color-scheme: dark)').addEventListener('change', ({ matches }) => {
				if (matches) {
					const theme = matches.matches ? 'dark' : 'light';
					let defaultTheme = currentThemeSetting === 'dark' ? 'dark' : 'light';
					localStorage.setItem('theme', defaultTheme);
					updateButton({ buttonEl: button, isDark: defaultTheme });
					updateThemeOnHtmlEl({ theme: defaultTheme });

					defaultTheme = theme;
				} else {
					const theme = matches.matches ? 'dark' : 'light';
					let defaultTheme = currentThemeSetting === 'light';
					localStorage.setItem('theme', defaultTheme);
					updateButton({ buttonEl: button, isLight: defaultTheme });
					updateThemeOnHtmlEl({ theme: defaultTheme });

					defaultTheme = theme;
				}
			});
			/*const defaultTheme = currentDefaultSetting === 'dark' ? 'light' : 'dark';
			/*if (localStorage === defaultTheme) {
				return;
			} else if (localStorage !== defaultTheme) {*/
			/*e.preventDefault();
			localStorage.setItem('theme', defaultTheme);
			updateButton({ buttonEl: button, isDark: defaultTheme === 'dark' });
			updateThemeOnHtmlEl({ theme: defaultTheme });

			currentDefaultSetting = defaultTheme;

			/*let currentThemeSetting = calculateSettingAsThemeString({ systemSettingDark });
			currentThemeSetting(currentThemeSetting);*/
			/*const systemSettingDark = ({ systemSettingDark }) => {
				if (systemSettingDark.matches) {
					return 'dark';
				}

				return 'light';
			};
			return systemSettingDark({ systemSettingDark });
			/*return systemSettingDark === 'system';*/
		});
	}, [systemSetting]);

	return (
		<button
			className='sideBar-btn'
			data-theme-toggle
			aria-label='light Mode'>
			Dark Mode
		</button>
	);
}
