import React, { useState, useLayoutEffect, useRef, useContext } from 'react';
import $ from 'jquery';
import StoreApi from '../../Utils/StoreApi';

export default function BottomBar({ barTitle, barId }) {
	const [open, setOpen] = useState(false);
	const [newBar, setNewBar] = useState(barTitle);
	const { updateBar } = useContext(StoreApi);

	/* Keep Input Width Auto While Typing */

	const [width, setWidth] = useState(0);
	const span = useRef(null);

	useLayoutEffect(() => {
		setWidth(span.current.offsetWidth);
	}, [newBar]);

	/* On Clicking Buttons Events */

	const blur = () => {
		setOpen(!open);

		if (newBar.trim().length <= newBar.length) {
			setNewBar(newBar.trim());
		}
		newBar.trim().length === 0 ? setNewBar(barTitle) : updateBar(newBar.trim(), barId);
	};

	/* On Key Down Events */

	const keyDown = (e) => {
		if (e.key === 'Enter') {
			document.querySelector('.bottomBar-input').blur();
		} else if (e.key === 'Escape') {
			document.querySelector('.bottomBar-input').blur();
			$(document).trigger('click');
		}
	};

	return (
		<div className='bottomBar-toolbar'>
			<span
				className='bottomBar-hide'
				ref={span}>
				{newBar}
			</span>
			{open ? (
				<div id='blur'>
					<input
						style={{ width }}
						value={newBar}
						maxLength='512'
						onKeyDown={keyDown}
						onChange={(e) => setNewBar(e.target.value)}
						onFocus={(e) => e.target.select()}
						onBlur={blur}
						className='bottomBar-input'
						autoFocus
						name='text'
						type='text'
						autoComplete='off'
					/>
				</div>
			) : (
				<span
					id='blur'
					onClick={() => setOpen(!open)}
					className='bottomBar-wrapper'>
					{newBar}
				</span>
			)}
		</div>
	);
}
