import React, { useState, useLayoutEffect, useRef, useContext } from 'react';
import $ from 'jquery';
import ClearIcon from '@mui/icons-material/Clear';
import Cards from './Cards';
import Titles from './Titles';
import StoreApi from '../../Utils/StoreApi';
import { Draggable, Droppable } from 'react-beautiful-dnd';
import InputCards from './InputCards';

export default function List({ list, cards, index }) {
	const [open, setOpen] = useState(false);
	const [text, setText] = useState('');
	const { addMoreCard } = useContext(StoreApi);

	const inputRef = useRef(null);

	/* Keep Textarea Height Auto While Typing */

	const [height, setHeight] = useState(0);
	const div = useRef(null);

	useLayoutEffect(() => {
		setHeight(div.current.scrollHeight);
	}, [text]);

	/* On Clicking Buttons Events */

	const btn1 = () => {
		if (text.trim().length === 0) {
			inputRef.current.focus();
			setText('');
			setOpen(open);
		} else {
			addMoreCard(text.trim(), list.id);
			setText('');
			setOpen(!open);
		}
	};

	const btn2 = (e) => {
		$(document).trigger('click');
		setOpen(!open);
		setText('');
	};

	/* On Key Down Events */

	const keyDown = (e) => {
		if (e.key === 'Enter') {
			document.querySelector('.inputCard-btn').click();
		} else if (e.key === 'Escape') {
			$(document).trigger('click');
			setOpen(!open);
			setText(text.trim());
			if (text.trim().length === 0) {
				setText('');
			}
		}
	};

	/* Clicking Outside The Div Closing It */

	$(document).on('click', (e) => {
		if ($(e.target).closest('#blur').length === 0 && open === true) {
			setOpen(!open);
			setText('');
		}
	});

	return (
		<Draggable
			draggableId={list.id}
			index={index}>
			{(provided) => (
				<div
					ref={provided.innerRef}
					{...provided.draggableProps}
					className='Lists'>
					<div className='List-wrapper'>
						<div
							className='Titles'
							{...provided.dragHandleProps}>
							<Titles
								title={list.title}
								listId={list.id}
							/>
						</div>
						<Droppable
							droppableId={list.id}
							index={index}
							type='card'>
							{(provided) => (
								<>
									<div
										className='List overflow'
										ref={provided.innerRef}
										{...provided.droppableProps}>
										{cards.map((card, index) => (
											<Cards
												key={card.id}
												card={card}
												text={card.text}
												cardId={card.id}
												index={index}
											/>
										))}
										{provided.placeholder}
										<div>
											{open && (
												<div
													id='blur'
													className='inputCard-inputWrapper'>
													<textarea
														style={{ height }}
														autoFocus
														ref={inputRef}
														onKeyDown={keyDown}
														onChange={(e) => setText(e.target.value)}
														placeholder='Enter a title for this card...'
														className='inputCard-input'
														value={text}
														name='text'
														type='text'
														autoComplete='off'
													/>
													<div>
														<button
															className='inputCard-btn'
															onClick={btn1}>
															Add card
														</button>
														<ClearIcon
															className='inputCard-icon'
															onClick={btn2}
														/>
													</div>
												</div>
											)}
										</div>
									</div>
									{setOpen && (
										<InputCards
											open={open}
											setOpen={setOpen}
										/>
									)}
									<div className='inputCard-wrapper'>
										<div
											className='inputCard-hide'
											ref={div}>
											{text}
										</div>
									</div>
								</>
							)}
						</Droppable>
					</div>
				</div>
			)}
		</Draggable>
	);
}
