import React, { useContext } from 'react';
import $ from 'jquery';
import PhotosBar from './PhotosBar';
import ColorsBar from './ColorsBar';
import ToggleBtn from './ToggleBtn';
import Buttons from './Buttons';
import StoreApi from '../../Utils/StoreApi';

export default function SideBars() {
	const { btnImages, btnColors } = useContext(StoreApi);

	const btn1 = () => {
		document.querySelector('.sideBar-bar').style.width = '350px';
	};

	const btn2 = () => {
		document.querySelector('.sideBar-bar').style.width = '0';
		$(document).trigger('click');
	};

	const btn3 = () => {
		document.querySelector('.sideBar-bar.sideBar-bar-photos').style.width = '350px';
	};

	const btn4 = () => {
		document.querySelector('.sideBar-bar.sideBar-bar-colors').style.width = '350px';
	};

	/* Clicking Outside The Div Closing It */

	$(document).on('click', (e) => {
		if ($(e.target).closest('#blur').length === 0) {
			document.querySelector('.sideBar-close-btn').click();
		}
	});

	return (
		<>
			<ToggleBtn />
			<button
				id='blur'
				onClick={btn1}
				className='sideBar-btn'>
				Background
			</button>
			<div
				id='blur'
				className='sideBar-bar'>
				<div className='sideBar-head'>
					<h3 className='sideBar-header'>Background</h3>
					<span
						onClick={btn2}
						className='sideBar-close-btn'>
						
					</span>
				</div>
				<hr className='sideBar-hr' />
				<div className='sideBar-wrapper'>
					<div className='sideBar-wrap'>
						<div
							className='sideBar-photos-wrap'
							style={{ backgroundColor: btnImages }}>
							<div
								onClick={btn3}
								className='sideBar-photos'></div>
						</div>
						<div className='sideBar-text'>Photos</div>
					</div>
					<div className='sideBar-wrap'>
						<div
							className='sideBar-colors-wrap'
							style={{ backgroundColor: btnColors }}>
							<div
								onClick={btn4}
								className='sideBar-colors'></div>
						</div>
						<div className='sideBar-text'>Colors</div>
					</div>
				</div>
				<PhotosBar btn2={btn2} />
				<ColorsBar btn2={btn2} />
				<hr className='sideBar-hr' />
				<Buttons />
			</div>
		</>
	);
}
